@import url(https://fonts.googleapis.com/css2?family=Alata&family=Yeseva+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about-me{padding:104px 0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;background-color:var(--dark)}.about-me #Color\/Palette\/Blue-01{fill:var(--blue)}.about-me div{text-align:center}.about-me div .about-me-text{width:85%;font-size:18px;text-align:center;line-height:30px;color:var(--light)}.about-me div .about-me-text:nth-of-type(1){margin:50px 0 10px 0}@media screen and (min-width: 768px){.about-me div .about-me-text{width:393px}}

.title{font-family:var(--sans-title);font-size:48px;text-transform:lowercase}@media screen and (min-width: 768px){.title{font-size:64px}}

.contact{font-size:18px;text-align:center;padding:50px 0 150px 0;background-color:var(--dark);color:var(--light)}.contact .email-container{margin:100px 0 50px 0}.contact .email-container .email-desc{width:85%;line-height:30px;margin:0 auto;color:var(--blue)}@media screen and (min-width: 768px){.contact .email-container .email-desc{width:393px}}.contact .socials-container{padding:0 0 75px 0}.contact .socials-container .social-title{text-transform:lowercase}.contact .socials-container .social-icons{margin:25px 0 0 0}.contact .socials-container .social-icons .social-icon{margin:0 23px}.contact .socials-container .social-icons .social-icon:hover svg{stroke:var(--highlight)}

.tag{position:relative;font-size:12px;text-transform:uppercase;color:var(--blue)}.tag:before{content:'';position:absolute;top:2px;left:-10px;width:2px;height:13px;background-color:var(--highlight)}.adjust-position{margin-left:12px}

.landing .landing-content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:0 0 10vh 0;height:70vh}.landing .landing-content .landing-title{font-family:var(--cursive);font-size:48px;font-weight:400;text-align:center;margin:0;color:var(--dark)}.landing .landing-content .landing-secondary{max-width:328px;font-size:18px;text-align:center;margin:32px 0;color:var(--blue)}

.project-container{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}@media screen and (min-width: 1024px){.project-container{margin:100px 0}}.project-container .project-image{width:90%;height:90vw;background-position:center;background-size:cover}@media screen and (min-width: 768px){.project-container .project-image{width:450px;height:450px}}@media screen and (min-width: 1024px){.project-container .project-image{width:400px;height:400px}}@media screen and (min-width: 1280px){.project-container .project-image{width:461px;height:461px}}.project-container .project{width:90%;padding:50px 0 100px 0}.project-container .project .project-title{font-family:var(--cursive);font-size:24px;font-weight:400;color:var(--dark);margin-top:0;margin-left:0}.project-container .project .project-desc{font-size:14px;margin:22px 0 40px 0;color:var(--blue)}.project-container .project a{position:relative;display:block;text-decoration:none;color:var(--dark);margin:10px 0 0 0}.project-container .project a .arrow{position:absolute;top:0;left:40%}@media screen and (min-width: 768px){.project-container .project{width:450px}.project-container .project .project-title{font-size:36px}}@media screen and (min-width: 1024px){.project-container .project{width:240px;padding:0}.project-container .project .project-title{text-transform:lowercase}}.row{-webkit-flex-direction:column;flex-direction:column}@media screen and (min-width: 1024px){.row{-webkit-flex-direction:row;flex-direction:row}}.row-reverse{-webkit-flex-direction:column;flex-direction:column}@media screen and (min-width: 1024px){.row-reverse{-webkit-flex-direction:row-reverse;flex-direction:row-reverse}}

.work-samples .projects-container{width:90%;margin:92px auto}@media screen and (min-width: 1024px){.work-samples .projects-container{width:800px}}@media screen and (min-width: 1280px){.work-samples .projects-container{width:922px}}.work-samples .view-all{font-weight:400;margin:0 0 123px 0;text-align:center;text-transform:uppercase;color:var(--dark);cursor:pointer}


.project{position:relative;transition:all .25s ease-in-out}.project .project-title{margin:0;text-transform:lowercase;cursor:pointer}.project .project-info .project-bio{width:90%;color:var(--blue)}@media only screen and (min-width: 768px){.project .project-info .project-bio{width:350px}}.project .project-info .project-links .project-link{position:relative;font-size:18px;font-weight:700;text-decoration:none;color:var(--dark)}.project .project-info .project-links .project-link .arrow{position:absolute;top:2px}.project .project-info .project-links .project-link:nth-of-type(1){margin:0 60px 50px 0}.project .heroku-warning{display:none}@media only screen and (min-width: 1280px){.project .heroku-warning{display:inline;display:initial;position:absolute;width:416px;top:-25%;left:35%;color:var(--dark);opacity:.2}.project .heroku-warning .heroku-warning-title{font-size:36px;font-weight:400}.project .heroku-warning p{font-size:18px;margin:0}}.move-project{margin:30px 0 0 0}

.projects{padding:0 20px 100px 20px}@media only screen and (min-width: 768px){.projects{padding:25px 200px 200px 200px;text-align:left}}

.footer{background-color:var(--grey);padding:35px 0;text-align:center}.footer .created-by{position:relative;text-decoration:none;color:var(--light);word-spacing:5px}.footer .created-by .created-by-text{margin:0 20px}.footer .created-by .heart{position:absolute;top:-30px;left:36.5%;margin-top:30px;-webkit-animation-name:bounce;animation-name:bounce;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes bounce{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(10px);transform:translateY(10px)}0%{-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes bounce{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(10px);transform:translateY(10px)}0%{-webkit-transform:translateY(0);transform:translateY(0)}}

:root {
    --cursive: 'Yeseva One', cursive;
    --sans: 'Alata', sans-serif;
    --sans-title: 'Montserrat', sans-serif;
    --light: #fff;
    --dark: #131315;
    --grey: #212124;
    --blue: #747585;
    --highlight: #ff0f3f;
}

body {
    font-family: 'Alata', sans-serif;
    font-family: var(--sans);
}

.nav{text-align:center}.nav ul{margin:0;padding:30px 0}.nav ul li{display:inline-block;font-size:18px;margin:0 41px;color:var(--blue);cursor:pointer}.nav ul .active{color:var(--highlight)}

