.about-me {
    padding: 104px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--dark);

    // change avatar background color
    #Color\/Palette\/Blue-01 {
        fill: var(--blue);
    }

    div {
        text-align: center;
        
        .about-me-text {
            width: 85%;
            font-size: 18px;
            text-align: center;
            line-height: 30px;
            color: var(--light);
    
            &:nth-of-type(1){
                margin: 50px 0 10px 0;
            }
    
            @media screen and (min-width: 768px) {
                width: 393px;
            }
        }
    }
}